.topbar{height:5.625rem;z-index:0;font-family:Poppins;display:flex;align-items:center;justify-content:space-between;padding-left:7.5rem;padding-right:7.5rem}.topbar .topbar__title{font-size:1.5rem;font-weight:500;color:#333333}.topbar .topbar__greeting{font-size:1.125rem;font-weight:400;margin:0;color:#000000}.topbar .topbar__greeting .topbar__greeting__icon{font-size:0.9rem}.topbar .topbar__actions{display:flex;align-items:center}.topbar .topbar__actions .topbar__btn{padding:0.7rem 2rem;border-radius:30px;box-shadow:0 4px 4px 0 rgba(0,0,0,0.25);background-color:#49208d;border:1px solid #49208d;outline:none;font:inherit;font-size:12px;color:#ffffff;margin:0 auto;display:block;transition:transform, box-shadow, 0.2s ease;text-decoration:none}.topbar .topbar__actions .topbar__btn:first-child{margin-right:1rem;background-color:#fff;color:#49208d}.topbar .topbar__actions .topbar__btn:hover{transform:translateY(-2px);box-shadow:0 5px 7px 0 rgba(0,0,0,0.2)}.topbar .topbar__actions .topbar__btn:active{transform:translateY(0);box-shadow:0 2px 2px 0 rgba(0,0,0,0.35)}


